body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /*background-color: #e96d64;*/
  background-color: #bfe9ff;
  background: -webkit-gradient(linear, left top, right top, from(#ff6e7f), to(#bfe9ff));
  background: -webkit-linear-gradient(left, #ff6e7f, #bfe9ff);
  background: -o-linear-gradient(left, #ff6e7f, #bfe9ff);
  background: linear-gradient(90deg, #ff6e7f, #bfe9ff);
}




